function ArgazePage() {
  return (
    <FrameworkPage title="ARGaze: A Dataset of Eye Gaze Images for Calibration-Free Eye Tracking with Augmented Reality Headset">
      <Link to="/publications/">Show All Publications</Link>
      <h3>ARGaze: A Dataset of Eye Gaze Images for Calibration-Free Eye Tracking with Augmented Reality Headset</h3>
      <p>Zihan Yan, Yue Wu, Yifei Shan, Wenqian Chen, Xiangdong Li</p>
      <img style={{ maxWidth: '100%' }} src={argaze2Image} alt="argaze2" />
        <p>
          Eye-tracking is a widespread method in human-computer interaction. However, it is often criticised for the troublesome calibration with new users and scenes. Despite progress in machine learning-based eye tracking, preparing a qualified dataset remains challenging. We present ARGaze, a dataset of eye gaze images, for calibration-free eye tracking with AR headset. The dataset was derived from 25 participants who conducted eye gaze tasks in augmented reality and real-world scenes for approximately 30min. It comprises 1,321,968 pairs of eye images and corresponding world view in 50 videos. To validate the dataset, we implemented the SIFTNet- and ALSTM-FCN-hybrid model and compared the results with that of the state-of-the-art research. The results show that the dataset is of high compatibility with different machine learning models and it contains sufficient eye gaze-related features that enable the record low eye gaze estimation error by 3.70degree on average and 1.56degree on specific participant, without involving any pre-study calibrations across the participants. Guidance for dataset reuse and related implications for eye tracking design and evaluation are described. 
        </p>
      <Link to="/publications/">Show All Publications</Link>
    </FrameworkPage>
  )
}

function TowardsPage() {
  return (
    <FrameworkPage title="Towards moment-to-moment attention-aware interfaces by detecting subsecond-scale attention fluctuations through EEG">
      <Link to="/publications/">Show All Publications</Link>
      <h3>Towards moment-to-moment attention-aware interfaces by detecting subsecond-scale attention fluctuations through EEG</h3>
      <p>Shan Zhang*, Zihan Yan*, Shardul Sapkota, Shengdong Zhao, Wei-Tsang Ooi, Ye Qiyuan</p>
      <img style={{ maxWidth: '100%' }} src={towards2Image} alt="towards2" />
        <p>
        Moment-to-moment attention fluctuation can bring unwanted and even life-threatening outcomes. While there are previous studies pertaining to attention detection, none have studied sub-second-scale moment-to-moment attention fluctuations. To enable automatic detection of this occurrence, we utilized a novel paradigm in psychology— the gradCPT, which measures attention through response time variability measurements and classifies sub-second-scale moment-to-moment attention fluctuations as “in-the-zone” and “out-of-the-zone”. We developed an EEG-based classifier to classify both states with an accuracy of 73.4%. We then evaluated our classifier through a thought probe study in the video learning scenario. The probe study showed that our EEG-based classifier can classify on-task and mind-wandering during video learning and improve the state-of-the-art by 9%. The results demonstrated the effectiveness of EEG-based detection of moment-to-moment attention fluctuation, which can be used to develop more attention aware systems in other real-world scenarios.
        </p>
      <Link to="/publications/">Show All Publications</Link>
    </FrameworkPage>
  )
}

function ReducingPage() {
  return (
    <FrameworkPage title="Reducing Cognitive Loads in Parcel Scanning with Eye Tracking-based Augmented Reality Headset">
      <Link to="/publications/">Show All Publications</Link>
      <h3>Reducing Cognitive Loads in Parcel Scanning with Eye Tracking-based Augmented Reality Headset</h3>
      <p>Zihan Yan, Yufei Wu, Yiyang Li, Yifei Shan, Preben Hansen, Xiangdong Li</p>
      <img style={{ maxWidth: '100%' }} src={reducing2Image} alt="reducing2" />
        <p>
        Parcel scanning in warehouse is cognitively demanding as it involves processes e.g. barcode seeking, result confirming, and parcel relocating that have different cognitive requirements. It may deteriorate human workers’ productivity and well-being of working. Despite many ergonomic solutions to parcel scanning, worker’s cognition loads are less concerned. To reduce cognitive loads in parcel scanning, we present the eye tracking-based augmented reality headset which integrates foveated vision detection to leverage spontaneous barcode scanning and result confirmation. We evaluated the headset’s usability and its influence on workers’ cognition performance with two groups of participants using the headset and PDA, respectively. The result shows that the headset significantly reduced participants’ overall cognitive loads in barcode seeking and result confirmation, it maintained higher scanning efficiency and lower cognitive loads than the PDA across all the tasks. The headset outperformed the PDA with respect to overall usability and ease of use. 
        </p>
      <Link to="/publications/">Show All Publications</Link>
    </FrameworkPage>
  )
}

function GenderPage() {
  return (
    <FrameworkPage title="Gender Differences of Cognitive Loads in Augmented Reality-based Warehouse">
      <Link to="/publications/">Show All Publications</Link>
    <h3>Gender Differences of Cognitive Loads in Augmented Reality-based Warehouse</h3>
    <p>Zihan Yan, Yifei Shan, Kailin Yin, Yiyang Li, Xiangdong Li</p>
    <img style={{ maxWidth: '100%' }} src={gender2Image} alt="gender2" />
      <p>
      Augmented reality is emerging into contemporary warehouses and considerably improves human worker’s cognition abilities and overall productivity. Despite proven benefits, little is known about how the male and female workers cope with cognitive loads of different warehouse tasks. Therefore, we developed the augmented reality headset to help the participants facilitate parcel sorting tasks and conducted empirical studies to investigate the gender differences of cognitive loads. The results show that the female workers had significantly lower operational efficiency, higher visual attention, and higher memory loads than the male, but they quickly gained advantages in all these aspects as fewer gender differences were identified with more difficult tasks. The female workers had higher visual attention than the male in the process of information seeking and interpretation. There were constant differences of working memory between the female and male throughout all the processes. Implications for the gender difference of cognitive loads are discussed.
      </p>
      <Link to="/publications/">Show All Publications</Link>
    </FrameworkPage>
  )
}

